/* TermsPageContent„ÅÆ„Çπ„Çø„Ç§„É´ */

.terms-main {
  padding-top: 70px; /* „Éò„ÉÉ„ÉÄ„Éº50px + ‰ΩôÁôΩ20px */
  padding-bottom: 40px;
  background-color: #F5F2EB;
  min-height: 100vh;
}

.terms-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 60px; /* Ê®™„ÅÆpadding„ÇíÂ¢ó„ÇÑ„Åó„Å¶Ë™≠„Åø„ÇÑ„Åô„Åï„Çí‰øù„Å§ */
  background-color: #FFFFFF;
