  margin-top: 20px;
  margin-bottom: 20px;
}

/* „Éò„ÉÉ„ÉÄ„Éº„Çª„ÇØ„Ç∑„Éß„É≥ */
.terms-header {
  text-align: center;
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 2px solid #8FBC8F;
}

.terms-title {
  font-size: 32px;
  font-weight: bold;
  color: #8B4513;
  margin-bottom: 16px;
  font-family: 'Helvetica Neue', 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
}

.terms-description {
  font-size: 16px;
  color: #666666;
  line-height: 1.6;
}

.terms-updated {
  font-size: 14px;
  color: #888888;
  margin-top: 8px;
  font-style: italic;
}

/* „Ç≥„É≥„ÉÜ„É≥„ÉÑÂÖ®‰Ωì */
.terms-content {
