}

/* ÂêÑÊù°È†Ö„Çª„ÇØ„Ç∑„Éß„É≥ */
.terms-section {
  margin-bottom: 32px;
}

.terms-section-title {
  font-size: 20px;
  font-weight: bold;
  color: #2F4F2F;
  margin-bottom: 16px;
  padding-left: 12px;
  border-left: 4px solid #8FBC8F;
}

.terms-section p {
  margin-bottom: 12px;
}

.terms-section p:last-child {
  margin-bottom: 0;
}

/* „É™„Çπ„Éà */
.terms-list {
  padding-left: 20px;
  margin-bottom: 0;
}

.terms-list li {
  margin-bottom: 8px;
}

/* Ë≠¶Âëä„Éú„ÉÉ„ÇØ„Çπ */
.terms-warning {
  background-color: #FFF8DC;
  padding: 16px;
  border-radius: 8px;
  border: 1px solid #F0E68C;
  margin-bottom: 16px;
}

.terms-warning p {
  margin: 0;
  font-weight: bold;
  color: #8B4513;
}

/* Âπ¥ÈΩ¢Âà∂Èôê„Éú„ÉÉ„ÇØ„Çπ */
.terms-age-restriction {
  background-color: #FFE4E1;
  padding: 16px;
  border-radius: 8px;
  border: 1px solid #FFC0CB;
}

.terms-age-restriction p {
  margin: 0;
  font-weight: bold;
  color: #8B4513;
}

/* „ÅäÂïè„ÅÑÂêà„Çè„Åõ„Çª„ÇØ„Ç∑„Éß„É≥ */
.terms-contact {
  background-color: #8FBC8F;
  padding: 24px;
  border-radius: 12px;
  text-align: center;
  margin-top: 40px;
}

.terms-contact-title {
  font-size: 20px;
  font-weight: bold;
  color: #FFFFFF;
  margin-bottom: 16px;
}

.terms-contact p {
  color: #FFFFFF;
  margin: 0 0 12px 0;
}

.terms-contact-link {
  color: #FFFFFF;
  text-decoration: underline;
  font-weight: bold;
}

.terms-contact-link:hover {
  color: #E8F5E8;
}

.terms-contact-note {
  font-size: 14px;
  color: #E8F5E8;
  margin-top: 8px;
}

/* „É¨„Çπ„Éù„É≥„Ç∑„ÉñÂØæÂøú */
@media (max-width: 768px) {
  .terms-main {
    padding-top: 50px; /* „Éò„ÉÉ„ÉÄ„ÉºÂàÜ„Å†„Åë */
    padding-bottom: 0;
  }
  
  .terms-container {
    margin: 0; /* Â∑¶Âè≥„ÅÆÈöôÈñì„ÇíÂÆåÂÖ®„Å´„Å™„Åè„Åô */
    padding: 30px 20px; /* ‰∏ä‰∏ãpaddingÂ∞ë„ÅóÊ∏õ„Çâ„Åô */
    border-radius: 0; /* Ëßí‰∏∏„Çí„Å™„Åè„Åó„Å¶„Éï„É´„Çπ„ÇØ„É™„Éº„É≥ÊÑü */
    box-shadow: none; /* ÂΩ±„ÇÇ„Å™„Åè„Åô */
  }
  
  .terms-title {
